@import url('https://fonts.googleapis.com/css2?family=Alkatra:wght@400..700&display=swap');
.body
{
    background-color: white;
    height: 100vh;
    width: 100%;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 100vh;
}
.text
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
}
.notFound
{
    font-size: 20px;
    font-family: "Alkatra", system-ui;
    font-optical-sizing: auto;
    /* display: flex;
    justify-content: center;
    align-items: center; */
    text-align: center;
    text-wrap: balance;
}
.goBack
{
    font-size: 23px;
    width: 146px;
    height: 34px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ff8f00;
    border-radius: 22px;
    border: none;
    outline: none;
    color: white;
    margin: 23px 0px;
    cursor: pointer;
}
.returnHome
{
    font-size: 25px;
    width: 320px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #17a9d9;
    border-radius: 24px;
    border: none;
    outline: none;
    color: white;
    cursor: pointer;
}
.imageContainer
{
    display: flex;
    justify-content: center;
    align-items: center;
}
.image
{
    height: 84%;
    background-size: cover;
}
@media only screen and (width <= 1115px)
{
    .body
    {
        display: grid;
        grid-template-rows: 70vh 30vh;
        grid-template-columns: 100%;
    }
    .imageContainer{grid-row: 1;}
}
@media only screen and (width <= 538px)
{
    .notFound{width: 95%;}
    .goBack{margin-bottom: 19px;}
}
@media only screen and (width <= 414px)
{
    .image
    {
        width: 100%;
        height: auto;
    }
    .notFound{font-size: 4.9cqi;}
    .goBack
    {
        font-size: 5.8cqi;
        width: 35.1cqi;
        height: 8.8cqi;
        margin: 5cqi;
    }
    .returnHome
    {
        font-size: 6.1cqi;
        width: 77.5cqi;
        height: 10cqi;
    }
}