.slider
{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #311bca;
    border-radius: 34px;
    cursor: pointer;
    transition: 0.4s;
}

.slider:before
{
    position: absolute;
    content: '';
    height: 70%;
    width: 40%;
    left: 8%;
    bottom: 4px;
    background-color:  #a8e6cf;
    border-radius: 50%;
    transition: 0.4s;
}

#toggle[type='checkbox']
{
    display: none;
}

#toggle[type='checkbox']:checked + .slider
{
    background-color: #a8e6cf;
}

#toggle[type='checkbox']:checked + .slider:before
{
    transform: translateX(115%);
    background-color: #311bca;
}
