:root
{
    --iconsDistance : 13px;
}
#Navbar
{
    display: flex;
    justify-content: center;
    position: fixed;
    padding: 5px;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
}
.navComponents
{
    display: flex;
    align-items: center;
}
.navComponents:nth-child(1)
{
    margin-right: auto;
    justify-content: left;
}
.navComponents:nth-child(1) > span
{
    font-size: 32px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
}
.navComponents:nth-child(2)
{
    width: 600px;
    justify-content: center;
}
.webIcon
{
    padding: 0px 30px 0px 30px;
}
.navBtn
{
    margin-left : var(--iconsDistance);
    margin-right : var(--iconsDistance);
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    border-radius: 50%;
    border: none;
}
.navBtn:hover
{
    background-color: rgb(87, 181, 157);
    cursor: pointer;
}
#inputNav
{
    font-size: 23px;
    padding: 3px 3px 3px 8px;
    width: 100%;
    outline: none;
    border: none;
    border-bottom-left-radius: 15px;
}
#userFirstCharecter
{
    border-radius: 50%;
    background-color: orange;
    font-weight: 700;
    font-size: 25px;
    border: none;
    margin-left : var(--iconsDistance);
    margin-right : var(--iconsDistance);
    cursor: pointer;
}
.toggle-slider
{
    position: relative;
    width: 53px;
    height: 29px;
    margin-left : var(--iconsDistance);
    margin-right : var(--iconsDistance);
}
.slider
{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #311bca;
    border-radius: 34px;
    cursor: pointer;
    transition: 0.4s;
}

.slider:before
{
    position: absolute;
    content: '';
    height: 70%;
    width: 40%;
    left: 8%;
    bottom: 4px;
    background-color:  #a8e6cf;
    border-radius: 50%;
    transition: 0.4s;
}

#toggle[type='checkbox']
{
    display: none;
}

#toggle[type='checkbox']:checked + .slider
{
    background-color: #a8e6cf;
}

#toggle[type='checkbox']:checked + .slider:before
{
    transform: translateX(115%);
    background-color: #311bca;
}
.dropdown-menu
{
    position: relative;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.dropdown-content
{
    position: absolute;
    top: calc(100% + 0.25rem);
    opacity: 0;
    width: fit-content;
    padding: 3px;
    transition: opacity 0.3s ease-in-out , transform 0.3s ease-in-out;
    transform: translateY(-10px);
    pointer-events: none;
    background-color: coral;
    color: white;
    white-space: nowrap;
}
.dropdown-menu button:hover + .dropdown-content
{
    opacity: 1;
    transform: translateY(0px);
}
#accountOptions
{
    position: absolute;
    box-shadow: 0px 0px 2px 2px gray;
    list-style: none;
    padding: 0px;
    margin: 0px;
    top: 100%;
    transition: opacity 0.3s ease-in-out , transform 0.3s ease-in-out;
    transform: translateY(-10px);
    pointer-events: none;
    background-color: white;
    color: black;
}
#accountOptions li
{
    padding: 5px;
    cursor: pointer;
}
#accountOptions li:hover , #accountOptions li:hover span
{
    background-color: blue;
    color: white;
}
.search
{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 71%;
    border-top-right-radius: 15px;
    border: none;
    cursor: pointer;
}
.secondNav
{
    display: flex;
    align-items: center;
    justify-content: center;
}
.secondSearch , .secondInput
{
    display: none;
}
.inputNav2
{
    width: 58cqi;
    font-size: 23px;
    padding: 3px 3px 3px 8px;
    outline: none;
    border: none;
    margin: 5px 0px;
    border-bottom-left-radius: 15px;
}
.back
{
    background-color: transparent;
    outline: none;
    border: none;
    border-radius: 50%;
    padding:0px;
    display: flex;
    align-items: center;
    justify-content: center;
}
@media only screen and (width<=1101px)
{
    .webIcon{padding-right: 15px;}
    .navComponents:nth-child(2)
    {
        width: 400px;
    }
}
@media only screen and (width<=850px)
{
    .navComponents:nth-child(2)
    {
        display: none;
    }
    .secondSearch
    {
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        background-color: transparent;
        border: none;
    }
}
@media only screen and (width<=587px)
{
    .navComponents:nth-child(1) > span
    {
        display: none;
    }
}
@media only screen and (width<=450px)
{
    .toggle-slider{margin-left: 5px;}
    #userFirstCharecter , .navBtn{margin: 0px 5px;}
    .webIcon{padding-left: 10px;}
    .inputNav2{width: 61cqi;}
}